<template>
  <div>
    <div v-for="user in room.user" :key="user.id">
      <div @click="makeActive" v-if="!isCurrentUser(user.steamid64)">
        <div :class="{ active: isActiveRoom() }" class="p-2 m-1 rounded room">
          <div>
            <img :src="user.avatar" alt="avatar" class="avatar">
            <span class="ms-3">
          {{ user.username }}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name: "Room",
  props: {
    room: Object,
    activeRoom: Number,
    currentUser: Object,
  },
  methods: {
    isCurrentUser: function (steamid64) {
      return steamid64 == this.currentUser.steamid64;
    },
    isActiveRoom: function () {
      return this.room.id == this.activeRoom;
    },
    makeActive: function () {
      this.$emit("makeActive", this.room.id);
    },
  },
};
</script>

<style scoped>
.room{
  /* background-color: hsl(204, 6%, 10%); */
  transition: 200ms;
  cursor: pointer;
}
.room:hover{
  background-color: hsl(204, 6%, 15%);
  transition: 200ms;
}

.active {
  background-color: hsl(204, 6%, 13%);
  transition: 200ms;
}
.avatar{
  width: 40px;
  border-radius: 5rem;
  border: #242729 1px solid;
}
</style>