<template>
  <div class="chat-container-light rooms ms-2">
    <div v-for="room in rooms" :key="room.id">
      <Room :room="room" :activeRoom="activeRoom" @makeActive="makeActive" :currentUser="currentUser"/>
    </div>
  </div>
</template>


<script>
import Room from "./Room.vue";

export default {
  name: "Rooms",
  components: {
    Room,
  },
  props:{
      rooms:Array,
      activeRoom:Number,
      currentUser:Object
  },
  methods:{
      makeActive:function(roomId){
        this.$emit('makeActive',roomId)
      }
  }
};
</script>

<style scoped>
.rooms{
  overflow-y: scroll;
  height: 100%;
  scrollbar-width: thin;
}
</style>