<template>
  <div class="d-flex">
    <div :class="{ 'ownuser': ownUser() }" class="message">
      <span class="message-content">
        {{ message.content }}
      </span>
    </div>
  </div>
</template>


<script>
export default {
  name: "Message",
  props: {
    message: Object,
    currentUser: Object,
  },
  methods: {
    ownUser: function () {
      return this.currentUser.steamid64 == this.message.steamid64;
    },
  },
};
</script>
<style scoped>
.message {
  margin: 5px;
  border: hsl(0, 0%, 30%) 1px solid;
  border-radius: 5rem;
}
.message-content {
  padding: 20px;
}
.ownuser{
    margin-left: auto;
    background-color: hsl(0, 0%, 30%);
}
</style>