<template>
  <div class="chat d-flex flex-column" v-if="activeRoom">
    <div class="chat-header chat-container-light p-2 mb-auto">
      <img
        :src="activeUser.avatar"
        alt="avatar"
        class="avatar"
      />
      <span class="ms-3">
        {{ activeUser.username}}
      </span>
    </div>
    <div class="chat-body px-2" id="chat-body" @scroll="handleScroll">
      <div v-for="message in messages" :key="message.room">
        <Message :message="message" :currentUser="currentUser" />
      </div>
    </div>
    <div class="chat-input mt-auto d-flex">
      <input type="text" v-model="chatInput" class="p-2 m-1" @keydown.enter="sendMessage">
      <button class="btn btn-sm text-white" @click="sendMessage"><i class="fa-solid fa-paper-plane-top">Send</i></button>
    </div>
  </div>
  <div v-else>
    <div class="w-100 h-100 text-center">
      <p>All empty</p>
    </div>
  </div>
</template>


<script>
import Message from "./Message.vue";

export default {
  name: "Chat",
  components: { Message },
  props: {
    activeRoom: Number,
    activeUser: Object,
    rooms: Array,
    messages: Array,
    currentUser:Object,
    scrollData:Number
  },
  mounted(){
    this.get_room();
  }
  ,
  data(){
      return {
          chatInput:'',
          room:undefined,
      }
  },
  methods: {
    handleScroll:function(event){
      if (event.target.scrollTop==0){
        this.$emit('get_messages')
      }
    },
    sendMessage:function(){
      this.$emit('sendMessage',this.chatInput);
      this.chatInput='';
    }
    ,
    get_room: function () {
      var roomid = this.activeRoom;
      for (let room of this.rooms) {
        if (room.id == roomid) {
          for (let user of room.user) {
            if (user.steamid64 != this.currentUser.steamid64) {
              return user;
            }
          }
        }
      }
    },
  },
};
</script>

<style scoped>
.chat {
  width: 100%;
  height: 100%;
}
.chat-body{
  overflow: hidden scroll;

  scrollbar-width: thin;
}


.chat-input input {
  background-color: #202223;
  color: #fff;
  overflow: hidden;
  box-sizing: content-box;
  resize: none;
  width: 100%;
  min-height: 20px;
  height: 20px;
  outline: 0;
  border: 1px solid #fff;
  border-radius: 10px;
  padding: 5px 0px 5px 0px;
}

.avatar{
    width: 50px;
    border: solid 1px rgba(255, 255, 255, 0.377);
    border-radius: 5rem;

}

</style>