<template>
  <div class="container">
    <div class="row chat-container m-3 rounded" style="height: 80vh">
      <div class="col-4 p-0 d-flex flex-column h-100">
        <User :currentUser="currentUser" />
        <Rooms
          :rooms="rooms"
          :activeRoom="activeRoom"
          :currentUser="currentUser"
          @makeActive="makeActive"
        />
      </div>
      <div class="col-8 p-0 h-100">
        <Chat
          :rooms="rooms"
          :activeRoom="activeRoom"
          :messages="messages"
          :currentUser="currentUser"
          :activeUser="activeUser"
          @sendMessage="sendMessage"
          @get_messages="getMessages"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Chat from "./components/Chat.vue";
import Rooms from "./components/Rooms.vue";
import User from "./components/User.vue";

export default {
  name: "App",
  components: {
    Rooms,
    Chat,
    User,
  },
  methods: {
    filterUser: function (users) {
      for (let usr of users) {
        if (usr.steamid64 != this.currentUser.steamid64) {
          return usr;
        }
      }
    },
    makeActive: function (roomId) {
      if (this.activeRoom==roomId){
        return 0;
      }
      this.activeRoom = roomId;
      for (let room of this.rooms) {
        if (room.id == roomId) {
          for (let user of room.user) {
            if (user.steamid64 != this.currentUser.steamid64) {
              this.activeUser = user;
              this.createChat();
              return 0;
            }
          }
        }
      }
    },
    getMessages:function(){
      var data = { command: 'get_messages',to_user:this.activeUser.steamid64};
      if (this.messages.length!=0){
        data.from_range=this.messages[0].date_sent;
      }
      this.sendSocket(data);
    },
    sendMessage: function (message) {
      var data = { command: 'send_message',to_user:this.activeUser.steamid64, message: message };
      this.sendSocket(data);
    },
    sendSocket: function (data) {
      this.socket.send(JSON.stringify(data));
    },
    getUser: function () {
      var data = { command: "get_user" };
      this.sendSocket(data);
    },
    getRooms: function () {
      var data = { command: "get_chat" };
      this.sendSocket(data);
    },
    createChat: function () {
      var data = { command: "create_chat", to_user: this.activeUser.steamid64 };
      this.sendSocket(data);
    },
    handleData: function (data) {
      switch (data.command) {
        case "get_user":
          this.currentUser = data.data;
          break;
        case "get_chat":
          this.rooms = data.data;
          break;
        case "create_chat":
          this.getMessages()
          break;
        case "send_message":
          this.messages.push(data);
          var objDiv = document.getElementById("chat-body");
          objDiv.scrollTop = objDiv.scrollHeight;
          break;
        case "get_messages":
          if (this.messages.length!=0){
            var objDiv = document.getElementById("chat-body");
            objDiv.scrollTop = objDiv.scrollTop+10;
            this.messages = [...data.data,...this.messages]
          }else{
            this.messages = data.data
          }
          break;
      }
    },
  },
  data() {
    return {
      rooms: [],
      activeRoom: 0,
      activeUser: { steamid64: "", username: "", avatar: "" },
      messages: [],
      currentUser: { steamid64: "", username: "", avatar: "" },
      socket: null,
    };
  },
  created: function () {
    let self = this;
    this.socket = new ReconnectingWebSocket("ws://" + window.location.host + "/ws/api/");
    this.socket.onopen = function (event) {
      self.getRooms();
      self.getUser();
      if (self.activeRoom){
        self.createChat()
      }
    };
    this.socket.onmessage = function (event) {
      self.handleData(JSON.parse(event.data));
    };
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.chat-container {
  background-color: #131415;
  color: white;
}
.chat-container-light {
  background-color: #181a1b;
  color: white;
}
</style>
